<template>
    <div data-v-79886f21="" class="page">
        <div data-v-152ab910="" data-v-79886f21="" class="sub-header">
            <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
            <div data-v-152ab910="" class="title">评价</div><!----></div>
        <div data-v-79886f21="" class="main">
            <ul data-v-79886f21="" class="service" v-for="(item,index) in orderservers" :key="index">
                <li data-v-79886f21="">{{item.title}}</li>
                <li data-v-79886f21="">
                    <div data-v-79886f21=""
                         :class="{'stars':true ,active:item2.active}"
                         v-for="(item2,index2) in item.scores" :key="index2"
                         @click="SET_ORDERSERVER_NUMBER({index:index,index2:index2,score:item2.value})"
                    ></div>
                </li>
            </ul>

            <div data-v-79886f21="" class="content-wrap">
                <textarea data-v-79886f21=""
                          placeholder="来分享你的消费感受吧!" v-model="content"></textarea>
            </div>
            <button data-v-79886f21="" type="button" class="submit" @click="onSubmit">提交</button>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapState, mapMutations} from "vuex"

    export default {
        data() {
            return {
                content: ""
            }
        },
        mounted(){
            document.title=this.$route.meta.title;
        },
        created() {
            //安全认证
            this.$utils.safeUser(this);
            this.gid = this.$route.query.gid ? this.$route.query.gid : "";
            this.ordernum = this.$route.query.ordernum ? this.$route.query.ordernum : "";
            //评价项目选择
            this.getOrderServers()

            //控制权限
            this.isSubmit = true;
        },
        computed: {
            ...mapState({
                //评价项目选择
                orderservers: state => state.order.orderservers,
                uid: state => state.user.uid
            })
        },
        methods: {
            ...mapActions({
                //评价项目选项
                getOrderServers: "order/getOrderServers",
                //提交评价
                getHandReview: "order/getHandReview"
            }),
            ...mapMutations({
                //设置评论分数
                SET_ORDERSERVER_NUMBER: "order/SET_ORDERSERVER_NUMBER"
            }),
            //提交评价
            onSubmit() {
                if (this.isSubmit){
                    this.isSubmit = false
                    if (this.orderservers.length > 0) {
                        let rsdata = [];
                        for (let i = 0; i < this.orderservers.length; i++) {
                            rsdata.push({
                                gid: this.gid,
                                myid: this.uid,
                                rsid: this.orderservers[i].rsid,
                                score: this.orderservers[i].score
                            })
                        }
                        this.getHandReview({
                            gid: this.gid,
                            content: this.content,
                            ordernum: this.ordernum,
                            rsdata: JSON.stringify(rsdata),
                            success: () => {
                                this.$router.go(-1);
                            }
                        })
                    }

                }

            }
        }
    }
</script>

<style scoped>
    .page {
        width: 100%;
        min-height: 100vh;
        background-color: #FFFFFF;
    }

    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background-image: url("../../../assets/images/home/goods/back.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }

    .main {
        width: 100%;
        padding-top: 1.02rem;
    }

    .service {
        width: 100%;
        height: 1rem;
        border-bottom: 1px solid #EFEFEF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
    }

    .service li:nth-child(1) {
        font-size: 0.32rem;
        margin-left: 3%;
        margin-right: 15%;
    }

    .service li:nth-child(2) {
        display: flex;
        display: -webkit-flex;
    }

    .service .stars {
        width: 0.4rem;
        height: 0.4rem;
        background-image: url("../../../assets/images/user/orders/stars1.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        margin-right: 0.3rem;
    }

    .service .stars.active {
        background-image: url("../../../assets/images/user/orders/stars2.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .content-wrap {
        width: 100%;
        height: 4rem;
        text-align: center;
        overflow: hidden;
    }

    .content-wrap textarea {
        width: 95%;
        height: 90%;
        font-size: 0.32rem;
        margin-top: 0.3rem;
        outline: none;
        border: 0 none;
        resize: none;
    }

    .submit {
        width: 90%;
        height: 0.8rem;
        background-color: #E51B19;
        font-size: 0.28rem;
        color: #FFFFFF;
        text-align: center;
        line-height: 0.8rem;
        border: 0 none;
        border-radius: 4px;
        outline: none;
        margin: 0 auto;
        display: block;
        margin-top: 0.3rem;
    }
</style>